import React from 'react'
import * as Styled from './style'
import Icon from '../Icon'

const ProjectCard = (props) => {
    return(
            <Styled.Wrapper hoverActive={props.hoverActive}>
                <Styled.TitleBox>{props.title}</Styled.TitleBox>
                    <Styled.ContentBox>
                        <Styled.TeamBox>with {props.team}</Styled.TeamBox>
                        <Styled.DateBox>{props.date}</Styled.DateBox>
                        <Styled.DescBox>{props.desc}</Styled.DescBox>
                        <Styled.bottomBox>
                            <Styled.ViewMore>자세히 보기
                                <Icon
                                    name="ChevronRight"
                                    width="12"
                                    height="12"
                                />
                            </Styled.ViewMore>
                            <Styled.ThumbnailBox 
                                fluid={props.thumbnail.childImageSharp.fluid}
                                alt={props.title}
                            />
                            
                        </Styled.bottomBox>
                    </Styled.ContentBox>
            </Styled.Wrapper>
    );

}


export default ProjectCard;
